<template>
<v-app>
  <v-data-table
  :search="search"
  :headers="headers"
  :items="desserts"
  class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar
      color="info"
        flat
        dark
      >
        <v-toolbar-title>Les notifications</v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Search"
        single-line
        hide-details
      >
      </v-text-field>
        <v-dialog
          v-model="dialog"
          max-width="500px"
        >
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>
            <v-card-text>
               Voulez-vous marquer cette notification  comme lu ?
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="blue darken-1"
                text
                @click="close"
              >
                Non
              </v-btn>
              <v-btn
                color="blue darken-1"
                text
                @click="save"
              >
                Oui
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
     <template v-slot:item.description="{ item }">
      <div v-if="item.status == 'Non lu'">
          <h4>
             <v-icon color="info" small>mdi-bell-ring</v-icon>{{ item.description }}
          </h4>
      </div>
      <div v-else>
         <v-icon color="info" small>mdi-bell-ring</v-icon>
          <span
        dark
      >
        {{ item.description }}
      </span>
      </div>
    </template>
    <template v-slot:item.date="{ item }">
      <div v-if="item.status == 'Non lu'">
          <h4>
             {{ item.date }}
          </h4>
      </div>
      <div v-else>
          <span
        dark
      >
        {{ item.date }}
      </span>
      </div>
    </template>
    <template v-slot:item.status="{ item }">
      <div v-if="item.status == 'Non lu'">
          <h4>
             {{ item.status }}
          </h4>
      </div>
      <div v-else>
          <span
        dark
      >
        {{ item.status }}
      </span>
      </div>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        mdi-eye
      </v-icon>
    </template>
  </v-data-table>
</v-app>
</template>
<script>
import Axios from 'axios'
  export default {
    data: () => ({
      search: " ",
      etudiant: '',
      dialog: false,
      dialogDelete: false,
      headers: [
        { text: 'Description', value: 'description' },
        { text: 'status', value: 'status' },
        { text: 'Date', value: 'date' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        id:'',
        id_etd:'',
        status:'',
        description: '',
        date: '',
         },
      demandes:[],
      defaultItem: {
        id:'',
        id_etd:'',
        status:'',
        description: '',
        date: '',
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        if(localStorage.role == "etudiant"){
          Axios.get(
                "http://localhost:3000/notification"
              ).then((res) => {
                res.data.forEach((item) => {
                  if((item.id_etd == localStorage.id) && (item.role != localStorage.role)){
                    this.desserts.push({
                         id:item.id,
                         id_etd:item.id_etd,
                         description: item.description,
                         status:item.status,
                         date: item.date
          });
                  }
                })
              });
        }else{
            Axios.get(
                "http://localhost:3000/notification"
              ).then((res) => {
                res.data.forEach((item) => {
                     if(item.role != localStorage.role){
                          this.desserts.push({
                         id:item.id,
                         id_etd:item.id_etd,
                         description: item.description,
                         status:item.status,
                         date: item.date
          });
                     }
                })
              });
        }
      },

      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
              Axios.put("http://localhost:3000/demande/"+this.editedItem.id)
            .then(() => {
                     this.dialogDelete = true

            })
      },

      deleteItemConfirm () {
        this.desserts.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        this.editedItem.status="Lu"
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
            Axios.put("http://localhost:3000/notification/"+this.editedItem.id,{
          id_etd:this.editedItem.id_etd ,
          status: "Lu",
          date: this.editedItem.date,
          description: this.editedItem.description,
          id_resp: localStorage.id,
          id:this.editedItem.id,
          })
            .then(() => {
            })
           this.close();
      },
    },
  }
</script>